.second-section {
  color: $dark;
  &__right-wrapper {
    display: flex;
    position: relative;
  }
  &__year {
    position: relative;
    left: 655px;
    top: 60px;
    margin-top: 67px;
    font-weight: 700;
    font-size: 64px;
    line-height: 34px;
    // color: $dark;
    p {
      position: relative;
      left: 150px;
      top: -20px;
      font-weight: 700;
      font-size: 24px;
      line-height: 34px;
      text-transform: uppercase;
    }
  }
  &__text {
    position: relative;
    left: 600px;
    top: 190px;
    display: block;
    max-width: 448px;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 34px;
    // color: $dark;
  }
  &__triangle {
    position: absolute;
    left: 657px;
    top: 110px;
    z-index: -1;
  }
  
  &__left-wrapper {
    display: flex;
    margin: 0 auto;
  }
  &__ellipse {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    i {
      width: 20px;
      height: 20px;
    }
  }
  &__line {
    border-right: 1px dashed #000;
    height: 248px;
  }
}