.third-section {
  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  color: $dark;
  &__left-wrapper {
    margin-top: 152px;
    position: relative;
  }
  &__year {
    // padding-top: 152px;
    font-weight: 700;
    font-size: 64px;
    line-height: 34px;
    // color: $dark;
    p {
      position: relative;
      left: 150px;
      top: -20px;
      font-weight: 700;
      font-size: 24px;
      line-height: 34px;
      text-transform: uppercase;
    }
  }
  &__text {
    position: relative;
    left: 80px;
    top: 25px;
    display: block;
    max-width: 448px;
    font-weight: 400;
    font-size: 16px;
    line-height: 34px;
    color: #000000;
    max-width: 316px;
    margin-bottom: 138px;
  }
  &__penta {
    position: absolute;
    left: 0px;
    top: 490px;
    z-index: -1;
  }
}