@font-face {
  font-family: "Rubik";
  src: url("../fonts/Rubik-Bold.eot");
  src: url("../fonts/Rubik-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Rubik-Bold.woff") format("woff"), url("../fonts/Rubik-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Rubik";
  src: url("../fonts/Rubik-Regular.eot");
  src: url("../fonts/Rubik-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Rubik-Regular.woff") format("woff"), url("../fonts/Rubik-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Rubik", sans-serif;
}

.container {
  max-width: 1120px;
  margin: 0 auto;
}

.first-section {
  color: #333333;
}
.first-section__wrapper {
  position: relative;
  margin-left: 45px;
}
.first-section__year {
  padding-top: 152px;
  font-weight: 700;
  font-size: 64px;
  line-height: 34px;
}
.first-section__text {
  position: relative;
  left: 80px;
  top: 25px;
  display: block;
  max-width: 448px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 34px;
  margin-bottom: 25px;
}
.first-section__ellipse {
  position: absolute;
  left: 425px;
  top: 160px;
  z-index: -1;
}

.second-section {
  color: #333333;
}
.second-section__right-wrapper {
  display: flex;
  position: relative;
}
.second-section__year {
  position: relative;
  left: 655px;
  top: 60px;
  margin-top: 67px;
  font-weight: 700;
  font-size: 64px;
  line-height: 34px;
}
.second-section__year p {
  position: relative;
  left: 150px;
  top: -20px;
  font-weight: 700;
  font-size: 24px;
  line-height: 34px;
  text-transform: uppercase;
}
.second-section__text {
  position: relative;
  left: 600px;
  top: 190px;
  display: block;
  max-width: 448px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 34px;
}
.second-section__triangle {
  position: absolute;
  left: 657px;
  top: 110px;
  z-index: -1;
}
.second-section__left-wrapper {
  display: flex;
  margin: 0 auto;
}
.second-section__ellipse {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.second-section__ellipse i {
  width: 20px;
  height: 20px;
}
.second-section__line {
  border-right: 1px dashed #000;
  height: 248px;
}

.third-section {
  color: #333333;
}
.third-section .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.third-section__left-wrapper {
  margin-top: 152px;
  position: relative;
}
.third-section__year {
  font-weight: 700;
  font-size: 64px;
  line-height: 34px;
}
.third-section__year p {
  position: relative;
  left: 150px;
  top: -20px;
  font-weight: 700;
  font-size: 24px;
  line-height: 34px;
  text-transform: uppercase;
}
.third-section__text {
  position: relative;
  left: 80px;
  top: 25px;
  display: block;
  max-width: 448px;
  font-weight: 400;
  font-size: 16px;
  line-height: 34px;
  color: #000000;
  max-width: 316px;
  margin-bottom: 138px;
}
.third-section__penta {
  position: absolute;
  left: 0px;
  top: 490px;
  z-index: -1;
}

.fourth-section .container {
  display: flex;
}
.fourth-section__left-wrapper {
  position: relative;
}
.fourth-section__year {
  position: absolute;
  left: 48px;
  top: 95px;
  font-weight: 700;
  font-size: 64px;
  line-height: 34px;
  color: #FFFFFF;
}
.fourth-section__text {
  position: absolute;
  left: 135px;
  top: 165px;
  font-weight: 400;
  font-size: 16px;
  line-height: 34px;
  color: #FFFFFF;
  max-width: 321px;
}
.fourth-section__bg {
  position: absolute;
  left: 952px;
  top: 1081px;
  width: 752px;
  height: 298px;
  background-color: #A5E5F4;
  z-index: -1;
}
.fourth-section .right-wrapper__text {
  margin-bottom: 65px;
  padding-left: 48px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 34px;
  color: #333333;
}
.fourth-section .right-wrapper__cups {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
}
.fourth-section .right-wrapper__cups_img {
  height: 84px;
  width: 63px;
}
.fourth-section .right-wrapper__div {
  position: relative;
}
.fourth-section .right-wrapper__line {
  position: absolute;
  top: -40px;
  left: 30px;
  width: 288px;
  border-top: 1px dashed #000;
}
.fourth-section .right-wrapper__descr {
  position: absolute;
  top: 37px;
  left: 509px;
  display: flex;
  flex-direction: column;
}
.fourth-section .right-wrapper__ellipse {
  position: absolute;
  top: 30px;
  left: 490px;
}
.fourth-section .right-wrapper__ellipse img {
  width: 35px;
  height: 35px;
}
.fourth-section .right-wrapper__subtitle_line {
  width: 97px;
  border-top: 1px solid #333333;
}

.fifth-section__cards {
  width: 1120px;
  margin: 125px auto 0 auto;
  display: grid;
  grid-template: repeat(2, 265px)/repeat(4, 265px);
  grid-auto-rows: 265px;
  gap: 18px;
  grid-auto-flow: reverse row;
}
.fifth-section__card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
  border-radius: 25px;
}
.fifth-section__card:hover {
  background: #FFE267;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
  border-radius: 25px;
  cursor: pointer;
}
.fifth-section__card_vertical {
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
  border-radius: 25px;
  grid-column: 1/3;
}
.logo_img {
  width: 107px;
  height: 151px;
}

@media (max-width: 1200px) {
  .container {
    max-width: 960px;
  }
}
@media (max-width: 768px) {
  .container {
    max-width: 720px;
  }
  .first-section__ellipse {
    left: 166px;
  }
  .first-section__text {
    margin: 0;
    max-width: 330px;
  }
  .second-section .container {
    position: relative;
  }
  .second-section__left-wrapper {
    position: absolute;
    top: 37px;
    left: 230px;
  }
  .second-section__year {
    left: 280px;
    top: 10px;
  }
  .second-section__text {
    left: 205px;
    top: 150px;
    max-width: 320px;
  }
  .second-section__triangle {
    left: 280px;
    top: 60px;
  }
  .third-section__text {
    min-width: 275px;
    left: 37px;
    top: 15px;
  }
  .third-section__right-wrapper img {
    max-width: 370px;
    max-height: 280px;
  }
  .third-section__penta {
    left: -153px;
    top: 100px;
  }
  .fourth-section .container {
    display: block;
  }
  .fourth-section__img {
    width: 372px;
    height: 319px;
  }
  .fourth-section img {
    max-width: 100%;
    min-height: 100%;
    object-fit: cover;
  }
  .fourth-section__year {
    left: 75px;
    top: 40px;
  }
  .fourth-section__text {
    left: 50px;
    top: 110px;
    max-width: 225px;
  }
  .fourth-section__right-wrapper {
    max-width: 378px;
    position: relative;
  }
  .fourth-section__bg {
    left: 396px;
    top: 797px;
    width: 378px;
    height: 342px;
  }
  .fourth-section .right-wrapper__text {
    position: absolute;
    top: -440px;
    left: 372px;
    margin: 0;
    margin-left: 17px;
    padding: 0;
    min-width: 349px;
  }
  .fourth-section .right-wrapper__awards {
    position: absolute;
    top: -40px;
    left: 126px;
  }
  .fourth-section .right-wrapper__cups {
    display: block;
  }
  .fourth-section .cup-one {
    position: absolute;
    top: -50px;
    right: -310px;
  }
  .fourth-section .cup-two {
    position: absolute;
    top: -50px;
    right: -370px;
  }
  .fourth-section .right-wrapper__line {
    width: 113px;
    top: 0px;
    left: 380px;
  }
  .fourth-section .right-wrapper__ellipse {
    right: -590px;
    top: -20px;
    left: 500px;
  }
  .fourth-section .right-wrapper__descr {
    top: -13px;
  }
  .fifth-section__cards {
    width: 745px;
    margin: 120px auto 0 auto;
    display: grid;
    grid-template: repeat(2, 227px)/repeat(3, 227px);
    grid-auto-rows: 227px;
    gap: 15px;
    grid-auto-flow: row;
  }
  .fifth-section__card_vertical {
    flex: none;
    justify-content: start;
    grid-column: 1/1;
  }
  .fifth-section__descr_dotes {
    display: none;
  }
  .fifth-section .logo_img {
    width: 102px;
    height: 144px;
  }
}
@media (max-width: 375) {
  .container {
    max-width: 100%;
    padding: 0 10px;
  }
  .first-section__year {
    padding: 0;
    margin: 0;
    margin-top: 127px;
  }
  .first-section__text {
    left: 40px;
  }
  .first-section__ellipse {
    top: 110px;
    left: -40px;
  }
  .first-section__text {
    margin: 0;
    max-width: 300px;
  }
  .second-section__right-wrapper {
    display: block;
  }
  .second-section__year {
    margin-top: 196px;
    left: 95px;
    top: 20px;
  }
  .second-section__text {
    left: 125px;
    top: 10px;
    max-width: 220px;
  }
  .second-section__triangle {
    transform: rotate(342deg);
    left: -80px;
  }
  .second-section__left-wrapper {
    top: -170px;
    left: 50px;
    height: 222px;
  }
  .second-section__line {
    height: 100%;
  }
  .third-section .container {
    margin: 0 auto;
    display: block;
  }
  .third-section__left-wrapper {
    margin-top: 103px;
  }
  .third-section__text {
    margin: 0;
    margin-top: 40px;
    left: 38px;
    top: -25px;
  }
  .third-section__right-wrapper__img {
    max-width: 333px;
    max-height: 278px;
  }
  .third-section__right-wrapper__img img {
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
  }
  .third-section__penta {
    left: 203px;
    top: 504px;
    transform: rotateY(180deg);
  }
  .fourth-section__bg {
    left: 10px;
    top: 1200px;
    width: 370px;
    height: 342px;
  }
  .fourth-section__left-wrapper {
    width: 100%;
  }
  .fourth-section__img {
    position: absolute;
    left: 0;
