  }
  .fourth-section__year {
    top: 340px;
  }
  .fourth-section__text {
    left: 140px;
    top: 400px;
  }
  .fourth-section .right-wrapper__text {
    text-align: start;
    top: 10px;
    left: 0px;
  }
  .fourth-section .right-wrapper__awards {
    top: 640px;
    left: -243px;
  }
  .fourth-section .right-wrapper__div {
    display: none;
  }
  .fifth-section .container {
    position: relative;
  }
  .fifth-section__cards {
    position: absolute;
    top: 680px;
    left: 50px;
    max-width: 326px;
    margin: 30px auto 0 auto;
    display: grid;
    grid-template: repeat(3, 157px)/repeat(2, 145px);
    grid-auto-rows: 157px;
    gap: 10px;
    grid-auto-flow: row;
  }
  .fifth-section__card_vertical {
    flex: none;
    justify-content: start;
    grid-column: 1/1;
  }
  .fifth-section__descr_dotes {
    display: none;
  }
  .fifth-section .logo_img {
    width: 102px;
    height: 144px;
  }
}