.fifth-section {
  &__cards {
    width: 1120px;
    margin: 125px auto 0 auto;
    display: grid;
    grid-template: repeat(2, 265px) / repeat(4, 265px);
    grid-auto-rows: 265px;
    gap: 18px;
    grid-auto-flow: reverse row;
  }
  &__card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
    border-radius: 25px;
    &:hover {
      background: #FFE267;
      box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
      border-radius: 25px;
      cursor: pointer;
    }
    &_vertical {
      display: flex;
      justify-content: space-around;
      align-items: center;
      box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
      border-radius: 25px;
      grid-column: 1 / 3;
    }
    &_active {
      
    }
  }
  &__descr {
    // &_logo {
    //   &:hover {
    //     filter: invert(1);
    //   }
    // }
  }
}
.logo_img {
  width: 107px;
  height: 151px;
