@media (max-width: 1200px) {
  .container {
    max-width: 960px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 720px;
  }
  .first-section {
    &__ellipse {
      left: 166px;
    }
    &__text {
      margin: 0;
      max-width: 330px;
    }
  }

  .second-section {
    .container {
      position: relative;
    }
    &__left-wrapper {
      position: absolute;
      top: 37px;
      left: 230px;
    }
    &__year {
      left: 280px;
      top: 10px;
    }
    &__text {
      left: 205px;
      top: 150px;
      max-width: 320px;
    }
    &__triangle {
      left: 280px;
      top: 60px;
    }
  }

  .third-section {
    &__text {
      min-width: 275px;
      left: 37px;
      top: 15px;
    }
    &__right-wrapper {
      img {
        max-width: 370px;
        max-height: 280px;
      }
    }
    &__penta {
      left: -153px;
      top: 100px;
    }
  }

  .fourth-section {
    .container {
      display: block;
    }
    &__left-wrapper {
    }
    &__img {
      width: 372px;
      height: 319px;
    }
      img {
        max-width: 100%;
        min-height: 100%;
        object-fit: cover;
      }
    &__year {
      left: 75px;
      top: 40px;
    }
    &__text {
      left: 50px;
      top: 110px;
      max-width: 225px;
    }
    &__right-wrapper {
      max-width: 378px;
      position: relative;
    }
    &__bg {
      left: 396px;
      top: 797px;
      width: 378px;
      height: 342px;
    }
    .right-wrapper__text {
      position: absolute;
      top: -440px;
      left: 372px;
      margin: 0;
      margin-left: 17px;
      padding: 0;
      min-width: 349px;
    }
    .right-wrapper__awards {
      position: absolute;
      top: -40px;
      left: 126px;
    }
    .right-wrapper__cups {
      display: block;
    }
    .cup-one {
      position: absolute;
      top: -50px;
      right: -310px;
    }
    .cup-two {
      position: absolute;
      top: -50px;
      right: -370px;
    }
    .right-wrapper__line {
      width: 113px;
      top: 0px;
      left: 380px;
    }
    .right-wrapper__ellipse {
      right: -590px;
      top: -20px;
      left: 500px;
    }
    .right-wrapper__descr {
      top: -13px;
    }
  }

  .fifth-section {
    &__cards {
      width: 745px;
      margin: 120px auto 0 auto;
      display: grid;
      grid-template: repeat(2, 227px) / repeat(3, 227px);
      grid-auto-rows: 227px;
      gap: 15px;
      grid-auto-flow: row;
    }
    &__card {
      &_vertical {
        flex: none;
        justify-content: start;
        grid-column: 1/1;
      }
    }
    &__descr_dotes {
      display: none;
    }
    .logo_img {
      width: 102px;
      height: 144px;
    }
  }
}

@media (max-width: 375) {
  .container {
    max-width: 100%;
    padding: 0 10px;
  }
  .first-section {
    &__wrapper {
    }
    &__year {
      padding: 0;
      margin: 0;
      margin-top: 127px;
    }
    &__text {
      left: 40px;
    }
    &__ellipse {
      top: 110px;
      left: -40px;
    }
    &__text {
      margin: 0;
      max-width: 300px;
    }
  }

  .second-section {
    &__right-wrapper {
      display: block;
    }
    &__year {
      margin-top: 196px;
      left: 95px;
      top: 20px;
    }
    &__text {
      left: 125px;
      top: 10px;
      max-width: 220px;
    }
    &__triangle {
      transform: rotate(342deg);
      left: -80px;
    }
    &__left-wrapper {
      top: -170px;
      left: 50px;
      height: 222px;
    }
    &__line {
      height: 100%;
    }
  }

  .third-section {
    .container {
      margin: 0 auto;
      display: block;
    }
    &__left-wrapper {
      margin-top: 103px;
    }
    &__text {
      margin: 0;
      margin-top: 40px;
      left: 38px;
      top: -25px;
    }
    &__right-wrapper {
      &__img {
        max-width: 333px;
        max-height: 278px;
        img {
          min-width: 100%;
          min-height: 100%;
          object-fit: cover;
        }
      }
    }
    &__penta {
      left: 203px;
      top: 504px;
      transform: rotateY(180deg);
    }
  }

  .fourth-section {
    &__bg {
      left: 10px;
      top: 1200px;
      width: 370px;
      height: 342px;
    }
    &__left-wrapper {
      width: 100%;
    }
    &__img {
      position: absolute;
      left: 0;
      top: 300px;
    }
    &__year {
      top: 340px;
    }
    &__text {
      left: 140px;
      top: 400px;
    }
    .right-wrapper__text {
      text-align: start;
      top: 10px;
      left: 0px;
    }
    .right-wrapper__awards {
      top: 640px;
      left: -243px;
    }
    .right-wrapper__div {
      display: none;
    }
  }

  .fifth-section {
    .container {
      position: relative;
    }
    &__cards {
      position: absolute;
      top: 680px;
      left: 50px;
      max-width: 326px;
      margin: 30px auto 0 auto;
      display: grid;
      grid-template: repeat(3, 157px) / repeat(2, 145px);
      grid-auto-rows: 157px;
      gap: 10px;
      grid-auto-flow: row;
    }
    &__card {
      &_vertical {
        flex: none;
        justify-content: start;
        grid-column: 1/1;
      }
    }
    &__descr_dotes {
      display: none;
    }
    .logo_img {
      width: 102px;
      height: 144px;
    }
  }
}