.first-section {
  color: $dark;
  &__wrapper {
    position: relative;
    margin-left: 45px;
  }
  &__year {
    padding-top: 152px;
    font-weight: 700;
    font-size: 64px;
    line-height: 34px;
    // color: $dark;
  }
  &__text {
    position: relative;
    left: 80px;
    top: 25px;
    display: block;
    max-width: 448px;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 34px;
    margin-bottom: 25px;
    // color: $dark;
  }
  &__ellipse {
    position: absolute;
    left: 425px;
    top: 160px;
    z-index: -1;
  }
}