.fourth-section {
  .container {
    display: flex;
  }
  &__left-wrapper {
    position: relative;
  }
  &__year {
    position: absolute;
    left: 48px;
    top: 95px;
    font-weight: 700;
    font-size: 64px;
    line-height: 34px;
    color: #FFFFFF;
  }
  &__text {
    position: absolute;
    left: 135px;
    top: 165px;
    font-weight: 400;
    font-size: 16px;
    line-height: 34px;
    color: #FFFFFF;
    max-width: 321px;
  }
  &__bg {
    position: absolute;
    left: 952px;
    top: 1081px;
    width: 752px;
    height: 298px;
    background-color: #A5E5F4;
    z-index: -1;
  }
  .right-wrapper {
    &__text {
      margin-bottom: 65px;
      padding-left: 48px;
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 34px;
      color: $dark;
    }
  }
  .right-wrapper {
    &__cups {
      position: relative;
      display: flex;
      justify-content: flex-start;
      align-items: flex-end;
      &_img {
        height: 84px;
        width: 63px;
      }
    }
    &__div {
      position: relative;
    }
    &__line {
      position: absolute;
      top: -40px;
      left: 30px;
      width: 288px;
      border-top: 1px dashed #000;
    }
    &__descr {
      position: absolute;
      top: 37px;
      left: 509px;
      display: flex;
      flex-direction: column;
    }
    &__ellipse {
      position: absolute;
      top: 30px;
      left: 490px;
      img {
        width: 35px;
        height: 35px;
      }
    }
    &__subtitle {
      &_line {
        width: 97px;
        border-top: 1px solid $dark;
      }
    }
  }
}